import {SQLiteTaskDao} from "./data/SQLiteTaskDao";
import {LocalStorageTaskDao} from "./data/LocalStorageTaskDao";
import {InMemoryDao} from "./data/InMemoryDao";

const NoteDao = (() => {
    if(typeof window !== "undefined") {
        if(window.openDatabase) {
            return new SQLiteTaskDao()
        }
        else {
            return new LocalStorageTaskDao(window.localStorage)
        }
    }
    else {
        return new InMemoryDao()
    }
})()
export default NoteDao;